'use strict';

(function() {
    var app = angular.module('app', [
        'ui.router',
        'app.home',
        'app.articles',
        'ngStorage',
        'ngMaterial'
    ]);

    bootstrapApplication();

    /********************
     * PRIVATE FUNCTIONS
     *******************/

    function bootstrapApplication() {
        angular.element(document).ready(function() {
            angular.bootstrap(document, ['app']);
        });
    }
    return app;
})();
'use strict';
(function () {
    angular.module('app')
           .run();
})();
