'use strict';
(function () {
    angular.module('app.home', [
        'ngSanitize',
        'ngMaterial'
    ]);
})();

(function () {
    'use strict';

    angular
        .module('app.home')
        .config(routerConfig);

    function routerConfig ($stateProvider, $urlRouterProvider) {

        function _transitionToEntryPoint ($state) {
            $state.transitionTo('home');
        }
        _transitionToEntryPoint.$inject = ["$state"];

        $urlRouterProvider
            .when('', _transitionToEntryPoint)
            .otherwise('/404');

        $stateProvider
            .state('entryPoint', {
                url: '/redirect',
                controller: _transitionToEntryPoint
            })
            .state('home', {
                url: '/',
                views: {
                    //header: {
                    //    templateUrl: 'views/partials/login/header.html'
                    //},
                    '@': {
                        templateUrl: 'home/views/home.html',
                        controller: 'Home',
                        controllerAs: 'viewModel',
                        title: 'home',
                        resolve: {
                            articles: ["articleFactory", function (articleFactory) {
                                return articleFactory.fetchArticles();
                            }]
                        }
                    }
                }
            })
            .state('404', {
                url: '/404',
                views: {
                    '@': {
                        templateUrl: 'common/views/404.html'
                    }
                }
            });
    }
    routerConfig.$inject = ["$stateProvider", "$urlRouterProvider"];
})();
'use strict';
(function () {
    angular.module('app.home')
        .factory('homeFactory', ['$http', HomeFactory]);

    function HomeFactory($http) {
        /***************
         * DECLARATIONS
         **************/
        var factory = {
            //Variables

            //Functions
            fetchArticlesContent: fetchArticlesContent
        };

        return factory;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function fetchArticlesContent() {
            var req = {
                method: 'GET',
                url: 'http://192.168.1.15:8080/blog-server/' + 'api/articles',
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            };
            return $http(req);
        }
    }
})();


'use strict';
(function() {
    angular
        .module('app.home')
        .controller('Home', ['$log', '$location', '$rootScope', 'articles', Home]);

    function Home($log, $location, $rootScope, articles) {

        $log.debug('[app.home] Loading Home controller');

        /***************
         * DECLARATIONS
         **************/

        var viewModel = this;
        viewModel.articles = articles;
        viewModel.goToArticle = goToArticle;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function goToArticle(articleId) {
            $location.path('article/' + articleId);
        }
    }
})();

