(function () {
    'use strict';

    angular
        .module('app.articles')
        .config(articleRoute);

    function articleRoute ($stateProvider) {
        $stateProvider
            .state('articles', {
                url: '/article/:articleId',
                views: {
                    '@': {
                        templateUrl: 'articles/views/article.html',
                        controller: 'Article',
                        controllerAs: 'viewModel',
                        title: 'article',
                        resolve: {
                            article: ["$stateParams", "articleFactory", function ($stateParams, articleFactory) {
                                return articleFactory.fetchArticleContent($stateParams.articleId);
                            }]
                        }
                    }
                }
            });
    }
    articleRoute.$inject = ["$stateProvider"];
})();

/**
 * @ngdoc filter
 * @name moment
 *
 * @description
 *  Dates filter using moment.js
 *
 * */
'use strict';
(function () {
    angular.module('app.articles')
        .filter('moment', [momentFilter]);

    function momentFilter() {
        return function(input) {
            return moment(input + '', 'x').format('D MMMM YYYY');
        }
    }
})();
'use strict';
(function () {
    angular.module('app.articles')
        .factory('articleFactory', ['$http', ArticleFactory]);

    function ArticleFactory($http) {
        /***************
         * DECLARATIONS
         **************/
        var factory = {
            //Variables

            //Functions
            fetchArticles: fetchArticles,
            fetchArticleContent: fetchArticleContent
        };

        return factory;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function fetchArticles() {
            var req = {
                method: 'GET',
                url: 'http://192.168.1.15:8080/blog-server/' + 'api/articles',
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            };
            return $http(req).then(function (response) {
                return response.data.articles;
            });
        }

        function fetchArticleContent(articleId) {
            var req = {
                method: 'GET',
                url: 'http://192.168.1.15:8080/blog-server/' + 'api/articles/article/' + articleId,
                headers: {
                    'Content-Type': 'application/json; charset=utf-8'
                }
            };
            return $http(req).then(function (response) {
                return response.data.article[0];
            });
        }
    }
})();


