'use strict';
(function () {
    angular
        .module('app')
        .controller('Menu', ['$location', Menu]);

    function Menu($location) {

        /***************
         * DECLARATIONS
         **************/

        var viewModel = this;
        viewModel.goToHome = goToHome;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function goToHome() {
            $location.path('');
        }

        function goToPartners() {
            $location.path('partners');
        }
    }
})();
