'use strict';
(function() {
    angular
        .module('app.home')
        .controller('Home', ['$log', '$location', '$rootScope', 'articles', Home]);

    function Home($log, $location, $rootScope, articles) {

        $log.debug('[app.home] Loading Home controller');

        /***************
         * DECLARATIONS
         **************/

        var viewModel = this;
        viewModel.articles = articles;
        viewModel.goToArticle = goToArticle;

        /******************
         * IMPLEMENTATIONS
         *****************/

        function goToArticle(articleId) {
            $location.path('article/' + articleId);
        }
    }
})();
